import { createAction } from 'redux-actions'
import axios from 'utils/axios'
import { Cookie } from 'utils/storage'

// 登录
export const login = (params) => dispatch => {
    return new Promise( (resolve, reject) => {
        axios.post('index/login', params)
            .then( res => {
                if (res.data.code === 200) {
                    const info = res.data
                    dispatch(createAction('SAVETOKEN')(info.data))
                    resolve(info)
                    axios.post('centre/index', {token: info.data})
                        .then( json => {
                            if (json.data.code === 200) {
                                const useinfro = json.data
                                dispatch(createAction('LOGIN')(useinfro))
                                resolve(useinfro)
                            } else {
                                console.log(json.data.error)
                            }
                            
                        })
                } else {
                    console.log(res.data.error)
                }
                        
            })
    })
}

export const getuserinfo = () => dispatch => {
    return new Promise( (resolve, reject) => {
        const token = Cookie.get('token')
        axios.post('centre/index', {token: token})
            .then( json => {
                if (json.data.code === 200) {
                    const useinfro = json.data
                    dispatch(createAction('LOGIN')(useinfro))
                    resolve(useinfro)
                } else {
                    console.log(json.data.error)
                }
                
            })
    })
}



