import { handleActions } from 'redux-actions'

const state = {
    videoList: [],
    play:[],
    opera:[],
    count:[],
    actor:[],
    recommend:[],
    hasMore: true,
    saveversion: []
}
export const video = handleActions({
    GET_VIDEO_LIST: (state, action) => {
        console.log(action.payload)
        state.play = action.payload.play.data,
        state.opera = action.payload.opera.data || []
        state.count = action.payload.count.data || []
        state.recommend = action.payload.recommend.data || []
        state.actor = action.payload.actor.data || []
        return {...state}
    },
    REFRESH_VIDEO_LIST: (state, action) => {
        console.log(action.payload)
        state.play = action.payload.play.data,
        state.opera = action.payload.opera.data || []
        state.count = action.payload.count.data || []
        state.recommend = action.payload.recommend.data || []
        state.actor = action.payload.actor.data || []
        return {...state}
    },
    TOGGLEPLAY: (state, action) => {
        console.log(action.payload)
        state.play = action.payload
        return {...state}
    },
    DOWN: (state, action) => {
        console.log(action.payload)
        state.saveversion = action.payload
        return {...state}
    },
    RENDER_VIDEO_LIST: state => ({...state})
}, state)


