import asyncComponent from './asyncComponent'

const _import_components = file => asyncComponent(() => import(`components/${file}`))
const _import_views = file => asyncComponent(() => import(`views/${file}`))

import NotFound from 'views/Error/404'

//含Layout视图
export const loyoutRouterMap = [
    { 
        path: '/', 
        name: '首页', 
        exact: true,
        component: _import_views('container')
    }
] 

// 不含Layout视图
export const notLoyoutRouterMap = [
    { 
        path: '/404', 
        name: '404', 
        component: NotFound
    },
    { 
        path: '/myorder/:type', 
        name: 'myorder', 
        component:  _import_views('myorder')
    },
    { 
        path: '/orderdetail', 
        name: 'orderdetail', 
        component:  _import_views('orderdetail')
    },
    { 
        path: '/express', 
        name: 'express', 
        component:  _import_views('express')
    },
    { 
        path: '/address', 
        name: 'address', 
        component:  _import_views('address')
    },
    { 
        path: '/editaddress', 
        name: 'editaddress', 
        component:  _import_views('editaddress')
    },
    { 
        path: '/myreview', 
        name: 'myreview', 
        component:  _import_views('myreview')
    },
    { 
        path: '/login', 
        name: '登陆', 
        component: _import_views('Login')
    },
    { 
        path: '/feedback', 
        name: '用户反馈',
        auth: true, 
        component: _import_views('Feedback')
    },
    { 
        path: '/system', 
        name: '系统设置',
        auth: true, 
        component: _import_views('System')
    },
    { 
        path: '/aboutme', 
        name: '关于我们', 
        component: _import_views('aboutme')
    },
    { 
        path: '/changepass', 
        name: 'changepass', 
        component: _import_views('changepass')
    },
    { 
        path: '/news', 
        name: 'news', 
        component: _import_views('news')
    },
    { 
        path: '/like', 
        name: 'like', 
        component: _import_views('like')
    },
    { 
        path: '/article/:id', 
        name: '文章', 
        component: _import_views('Article')
    },
    { 
        path: '/gooddetail/:id', 
        name: 'gooddetail', 
        component: _import_views('gooddetail')
    },
    { 
        path: '/collect', 
        name: '我的收藏', 
        component: _import_views('collect')
    },
    { 
        path: '/search', 
        name: '搜索',
        component: _import_views('Search')
    },
    { 
        path: '/setorder', 
        name: 'setorder',
        component: _import_views('setorder')
    },
]

// 所有视图
export const routes = loyoutRouterMap.concat(notLoyoutRouterMap)


