import { createAction } from 'redux-actions'
import axios from 'utils/axios'
import { Local } from 'utils/storage'

// 添加SearchList
export const getSearchList = (params) => dispatch => {
    return new Promise( (resolve, reject) => {
        axios.post('index/search', params)
            .then( res => {
                if (res.data.code === 200) {
                    const list = res.data.data
                    dispatch(createAction('GET_SEARCH_LIST')(list))
                    resolve(list)
                } else {
                    console.log(res.data.error)
                }
                console.log(params)
                console.log(res.data)
                dispatch(createAction('ADD_SEARCH_HISTORY')(params.name))
            })
    })
}

// 刷新当前SearchList 的内容
export const refreshSearchList = (params) => dispatch => {
    return new Promise( (resolve, reject) => {
        axios.post('index/search', params)
            .then( res => {
                if (res.data.code === 200) {
                    const list = res.data.data
                    dispatch(createAction('GET_SEARCH_LIST')(list))
                    resolve(list)
                } else {
                    console.log(res.data.error)
                }
                console.log(res.data)
                console.log(params)
                dispatch(createAction('ADD_SEARCH_HISTORY')(params.name))
            })
    })
}


//退出
export const delhistory = createAction('DELE_SEARCH_HISTORY')

// 重新渲染
export const renderSearchList = createAction('RENDER_SEARCH_LIST')



