import React from 'react'
import HeaderBar from 'components/HeaderBar'
import FooterBar from 'components/FooterBar'
const mainStyles = {
	width: '100%',
}
class App extends React.Component {
    render () {
        return (
            <div id="container">
                <main style={mainStyles}>
                	{this.props.children}
                </main>
            </div>
        )
    }
}
export default App



