import axios from 'axios'
import qs from 'qs'
import {store} from '../store'
import {showAlert} from 'actions/common'
import { Toast } from 'antd-mobile';



// 检查CODE值
function checkCode(res) {
    if (res.code !== 200) {
        
        throw new Error(res.error)
    }
    
    return res
}

function checkStatus(res) { 
    Toast.loading('Loading...', 1, () => {
        console.log('Load complete !!!');
      });
    // 结束
    return res
}
const baseURL = 'https://www.daxiku.cn/opera/index/'

// 备用路径  
// const baseURL = 'https://easy-mock.com/mock/5a83160c948cfd365a524088/apis/'
export default {
    get(url, params) {
        if (!url) return
        return axios({
            method: 'get',
            url: baseURL + url,
            params,
            timeout: 30000
        }).then(checkStatus)
    },
    post(url, data) {
        if (!url) return
        return axios({
            method: 'post',
            url: baseURL + url,
            data: qs.stringify(data),
            timeout: 30000
        }).then(checkStatus)
    },
    postFile(url, data) {
        if (!url) return
        return axios({
            method: 'post',
            url: baseURL + url,
            data
        }).then(checkStatus)
    }
}



