import { createAction } from 'redux-actions'
import axios from 'utils/axios'

// 添加news
export const addNews = createAction('ADD_NEWS')

// 删除 news
export const delNews = createAction('DEL_NEWS')



export const gethome = () => dispatch => {
    return new Promise( (resolve, reject) => {
        axios.get('index/index')
            .then( res => {
                if (res.data.code === 200) {
                    const list = res.data
                    dispatch(createAction('GET_LIST_OF_NEWS')(list))
                    resolve(list)
                } else {
                    console.log(res.data.error)
                }
                
               
            })
    })
}


export const getListOfid = (params) => dispatch => {
    return new Promise( (resolve, reject) => {
        axios.post('index/classifyopera', params)
            .then( res => {
                const list = res.data
                dispatch(createAction('REFRESH_LIST_OF_NEWS')(list))
                resolve(res)
            }).catch( err => {
                reject(err)
            })
    })
}



// 刷新当前news 的内容
export const refreshListOfNews = ({list, params}, newsIndex) => dispatch => {
    return new Promise( (resolve, reject) => {
        axios.get('index/index')
            .then( res => {
                console.log(res.data)
                const list = res
                dispatch(createAction('REFRESH_LIST_OF_NEWS')({list, newsIndex}))
                resolve(list)
            }).catch( err => {
                reject(err)
            })
    })
}


// 设置当前news 的下标
export const setNewsIndex = createAction('SET_NEWS_INDEX')


