import { handleActions } from 'redux-actions'
import { Cookie } from 'utils/storage'

const state = {
    isLogin: false,
    token: Cookie.get('token') || '',
    userinfo: [],
    collect: [],
    history: [],
    vip: [],
    vipdetail: [],
    coinrecord: [],
    historydetail: [],
    collectdetail: [],
    footerBarList: [
        {title: '首页', icon: 'home', path: '/'},
        {title: '西瓜视频', icon: 'video', path: '/video'},
        {title: '微头条', icon: 'comment', path: '/headline'},
        {title: `${Cookie.get('username')?'我的':'未登录'}`, icon: `${Cookie.get('username')?'account':'account1'}`, path: '/account'}
    ]
}
export const user = handleActions({
    LOGIN: (state, action) => {
        console.log(action.payload)
        state.userinfo = action.payload.userinfo.data,
        state.collect = action.payload.collect.data || []
        state.history = action.payload.history.data || []
        state.vip = action.payload.vip
        return {...state}
    },
    SAVETOKEN: (state, action) => {
        state.token = action.payload
        console.log(action.payload)
        Cookie.set({token: action.payload})
        console.log(Cookie.get('token'))
        return {...state}
    },
    VIP: (state, action) => {
        state.vipdetail = action.payload
        console.log(action.payload)
        return {...state}
    },
    COINRECORD: (state, action) => {
        state.coinrecord = action.payload
        console.log(action.payload)
        return {...state}
    },
    HISTORY: (state, action) => {
        state.historydetail = action.payload
        console.log(action.payload)
        return {...state}
    },
    COLLECT: (state, action) => {
        state.collectdetail = action.payload
        console.log(action.payload)
        return {...state}
    },
    SIGN_OUT: (state, action) => {
        state.userinfo = []
        state.collect = []
        state.history = []
        state.vip = []
        state.token = ''
        Cookie.remove(['token'])
        return {...state}
    },
    SHOW_LOGIN: state => {
        state.isLogin = true
        return {...state}
    },
    SET_FOOTER_LIST: (state, action) => {
        state.footerBarList[3] = action.payload
        return {...state}
    }

}, state)


