import { handleActions } from 'redux-actions'

const state = {
    searchList: [],
    hasMore: true,
    history:[]
}
export const search = handleActions({
    GET_SEARCH_LIST: (state, action) => {
        let list = action.payload
        state.searchList = state.searchList.concat(list)
        return {...state}
    },
    REFRESH_SEARCH_LIST: (state, action) => {
        state.searchList = action.payload
        return {...state}
    },
    ADD_SEARCH_HISTORY: (state, action) => {
        let his = state.history
        his.push(action.payload);
        let setArr = new Set(his);
        let newArr = Array.from(setArr);
        state.history = newArr;
        return {...state}
    },
    DELE_SEARCH_HISTORY: state => {
        state.history = [];
        return {...state}
    },
    RENDER_SEARCH_LIST: state => ({...state})
}, state)


